






























































































@import '@assets/styles/breakpoints.scss';

.board {
  @apply flex-row;

  @include breakpoint-max(lg) {
    @apply flex-col;
  }

  .side {
    flex: 0 0 385px;
    padding-right: 25px;

    @include breakpoint-max(lg) {
      @apply pr-0;
      margin-bottom: 25px;
    }

    .white-wrapper {
      padding: 30px 22px 20px;

      &:not(:last-child) {
        margin-bottom: 25px;
      }

      table {
        p {
          @apply leading-loose;
        }
      }
    }
  }

  .white-wrapper.main {
    @apply flex-1 flex flex-col;
    padding: 30px 22px 20px;

    .actions {
      @apply flex-1 items-end flex justify-end mt-6;

      i {
        transform: rotate(-90deg);
        @apply inline-block ml-4;
        font-size: 10px;
      }
    }
  }

  .tabs {
    @apply flex border-b border-grey-darkest;

    @include breakpoint-max(md) {
      @apply flex-col;
    }

    .tab {
      @apply text-base font-semibold text-black bg-grey-lightest px-4 cursor-pointer;
      margin-right: 1px;
      line-height: 41px;

      &.active {
        @apply bg-green-darker text-white;
      }
    }
  }
}

.accounts {
  > p {
    margin-bottom: 25px;
  }
}
