.modal {
  @apply fixed pin-t pin-r w-full h-full z-50 overflow-auto block;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
}

.modal-content {
  width: 400px;
  @apply pin-r pin-t h-full absolute;
}

